import{
	GET_COMPANIES
} from '../actions/company';

const initialState = {
  companies : []
};

export default function company(state = initialState, action = {}) {
  switch (action.type) {
    case GET_COMPANIES:
      return Object.assign({}, initialState, {companies: action.payload.promise});
    default:
      return state;
  }
}



