import React from 'react'
import { Table } from 'antd';
import api from '../../../api';
import PanelBox from '../../../components/PanelBox';
const columns = [{
  title: '登录日期',
  dataIndex: 'date',
  sorter: true,
  fixed:'left'
  //render: name => `${name.first} ${name.last}`,
 // width: '20%', 
}, {
  title: '登录时间',
  dataIndex: 'time',
 // width: '20%',
}, {
  title: '登录IP',
  dataIndex: 'ip',
}, {
  title: '登录地点',
  dataIndex: 'address',
}, {
  title: '是否异常',
  dataIndex: 'status',
}];
export default class LogInfo extends React.Component {
  constructor () {
    super()
    this.state = {
      data: [],
      pagination: {},
      loading: false,
    };
  }
  handleTableChange(pagination, filters, sorter) {
    const pager = this.state.pagination;
    pager.current = pagination.current;
    this.setState({
      pagination: pager,
    });
    this.fetch({
      results: pagination.pageSize,
      page: pagination.current,
      sortField: sorter.field,
      sortOrder: sorter.order,
      ...filters,
    });
  }
  fetch(params = {}) {
    this.setState({ loading: true });
    api.get('/logs', {
      params: {
        results: 10,
        ...params,
      },
      responseType: 'json'
    }).then((data) => {
      data = data.data;
      data = data.map((d)=>{
        console.log(d)
        let timeInfo = d['time'].split('/')
        let date = timeInfo[0]
        let time = timeInfo[1]
        console.log({date:date,time:time,...d})
        return {date:date,time:time,...d}
      })
     // console.log(data)
      const pagination = this.state.pagination;
      // Read total count from server
      // pagination.total = data.totalCount;
      pagination.total = 10;
      this.setState({
        loading: false,
        data: data,
        pagination,
      });
    });
  }
  componentDidMount () {
    this.fetch();
  }
  render() {
    return (
      
        <PanelBox title="登录日志">
          <Table columns={columns}
            rowKey={record => record.registered}
            dataSource={this.state.data}
            pagination={this.state.pagination}
            loading={this.state.loading}
            onChange={this.handleTableChange.bind(this)}
          />
        </PanelBox>
    );
  }
}




