var getFakeData = function(fields,count){
	var data = []
	for(let i=0;i<count;i++){
		var item = {}
		for(let key in fields){
			const k = Math.floor(Math.random()*fields[key].length);
			item[key] = fields[key][k]
		}
		data.push(item)
	}
	return data
	
}
module.exports = getFakeData


