import React from 'react'
import { Table } from 'antd';
import api from '../../api';
import PanelBox from '../../components/PanelBox';
import { Radio } from 'antd';
import { Input } from 'antd';
import MessageItem from './MessageItem';
import './style.less'

export default class Message extends React.Component {
  state = {
    data: [],
    loading: false,
  };
 
 
  fetch = (params = {}) => {
    this.setState({ loading: true });
    api.get('/messages', {
      params: {
        results: 10,
        ...params,
      },
      responseType: 'json'
    }).then((data) => {
      data = data.data;
     // console.log(data)
      // pagination.total = data.totalCount;
     this.setState({
        loading: false,
        data: data
      });
    });
  }
  componentDidMount () {
    this.fetch();
  }
  render() {
    var messages = this.state.data.map(function(d){
        return (
            <MessageItem time ={d.time} name={d.name} message={d.message} />
        )
    })
    return (
      <PanelBox title='消息'>
        <div className='messages'>
          {messages}
        </div>
      </PanelBox>
    );
  }
}



