import React from 'react'

import { Layout } from 'antd'
import { Row, Col } from 'antd';
import './style.less'
import api from '../../../api'
import PanelBox from '../../../components/PanelBox';

export default class UserInfo extends React.Component {
  constructor () {
    super()
    this.state = {
      userInfo:{}
    }
  }
  componentDidMount () {
    this.fetch();
  }
  fetch (params = {}) {
    this.setState({ loading: true });
    api.get('/my', {
      params: {
      },
      responseType: 'json'
    }).then((data) => {
      data = data.data;
      this.setState({
        userInfo: data
      });
    });
  }
  render () {

    return (
      <div>
        <PanelBox title="用户信息">
        <Row>
          <Col span={6}>
            用户名
          </Col>
          <Col span={6}>
            {this.state.userInfo.name}
          </Col>
        </Row>
        <Row>
          <Col span={6}>
            使用人
          </Col>
          <Col span={6}>
          {this.state.userInfo.admin}
          </Col>
        </Row>
        <Row>
          <Col span={6}>
            绑定手机
          </Col>
          <Col span={6}>
          {this.state.userInfo.phone}
          </Col>
        </Row>
        <Row>
          <Col span={6}>
            绑定邮箱
          </Col>
          <Col span={6}>
          {this.state.userInfo.email}
          </Col>
        </Row>
         </PanelBox>
      </div>
    )
  }
}



