import React from 'react'
import { Table } from 'antd';
import api from '../../api';
import PanelBox from '../../components/PanelBox';
import { Radio } from 'antd';
import { Input } from 'antd';
import { Select } from 'antd';
import { DatePicker } from 'antd';
import FileUpload from './FileUpload'
import { InputNumber } from 'antd';
import { Button } from 'antd';
const Option = Select.Option;
import './style.less'
const Search = Input.Search;
const RadioButton = Radio.Button;
const RadioGroup = Radio.Group;

export default class CompanyCreate extends React.Component {
  state = {
    fields: {},
    loading: false
  };
  fetch = (params = {}) => {
    this.setState({ loading: true });
    api.get('/company_fields', {
      responseType: 'json'
    }).then((data) => {
      data = data.data;
      this.setState({
        loading: false,
        fields: data
      });
    });
  }
  setInput(){
    var fields = this.state.fields
    var inputs  = []
    var fileInputs = []
    for(var key in fields){
      var input;
      if(fields[key]['type'] == 'text'){
        input = (
              <Input className='text-input' placeholder={fields[key]['label']} />
        )
      }
      if(fields[key]['type'] == 'select'){
        input = (
       
              <Select
                placeholder={"选择"+fields[key]['label']}
                optionFilterProp="children"
                className='select-input' 
              >
                <Option value="jack">Jack</Option>
                <Option value="lucy">Lucy</Option>
                <Option value="tom">Tom</Option>
              </Select>
        )
      }
      if(fields[key]['type'] == 'number'){
        input = (
       
              <InputNumber
                defaultValue={200}

              />
    
        )
      }
      if(fields[key]['type'] == 'rate'){
        input = (      
          <InputNumber
            defaultValue={70}
            min={0}
            max={100}
            formatter={value => `${value}%`}
            parser={value => value.replace('%', '')}
      
          />   
        )
      }
      if(fields[key]['type'] == 'date'){
        input = (
            <DatePicker  className='date-input' />
        )
      }
      if(fields[key]['type']!='img'){
        var inputItem = (
            <div className='input-item' key={key}>
              <div className='label'>{fields[key]['label']}</div>
              <div>
                {input}
              </div>
            </div>
        )
        inputs.push(inputItem)
      }

    }
    for(var key in fields){
      if(fields[key]['type']=='img'){
        var inputItem = (
          <div className='file-input-item' key={key}>
              <div className='label'>{fields[key]['label']}</div>
              <FileUpload/>
            </div>
        )
        fileInputs.push(inputItem)
      }
    }
    return (
      <div>
        <div className='input-form'>
          {inputs}
        </div>
        <div className='input-form'>
          {fileInputs}
         </div>
      </div>
    );
  }
  componentDidMount () {
    this.fetch();
  }
  render() {
    var inputs = this.setInput();
    return (
      <div className='company-create'>
        <div className='header'>
          <div className='title'>
            新建企业基本信息
          </div>
          <div className='buttons'>
            <Button className='button' type="primary">保存</Button>
            <Button className='button'>取消</Button>
          </div>
        </div>
         {inputs}
      </div>
    );
  }
}



