var axios = require('axios');
var MockAdapter = require('axios-mock-adapter');
var normalAxios = axios.create();
var mockAxios = axios.create();
// mock 数据
var mock = new MockAdapter(mockAxios);
var getFakeData = require('./getFakeData');
mock.onPut('/login').reply(200, require('./mock/user'));
mock.onGet('/logout').reply(200, {});
mock.onGet('/my').reply(200, require('./mock/user'));
mock.onGet('/menu').reply(200, require('./mock/menu'));
mock.onGet('/randomuser').reply((config) => {
  return new Promise(function(resolve, reject) {
    normalAxios.get('https://randomuser.me/api', {
      params: {
        results: 10,
        ...config.params,
      },
      responseType: 'json'
    }).then((res) => {
      resolve([200, res.data ]);
    }).catch((err) => {
      resolve([500, err ]);
    });
  });
});

mock.onGet('/companies').reply(function(config) {
  var companyFileds = require('./mock/companies')
  var companies = getFakeData(companyFileds,10)
  return [200, companies];
});
mock.onGet('/contracts').reply(function(config) {
  var contractsFileds = require('./mock/contracts')
  var contracts = getFakeData(contractsFileds,10)
  return [200, contracts];
});
mock.onGet('/scores').reply(function(config) {
  var scoresFileds = require('./mock/scores')
  var scores = getFakeData(scoresFileds,10)
  return [200, scores];
});
mock.onGet('/logs').reply(function(config) {
  var logFileds = require('./mock/logs')
  var logs = getFakeData(logFileds,10)
  return [200, logs];
});
mock.onGet('/messages').reply(function(config) {
  var messageFileds = require('./mock/messages')
  var messages = getFakeData(messageFileds,10)
  return [200, messages];
});
mock.onGet('/company').reply(function(config) {
  var companyFileds = require('./mock/company')
  var company = getFakeData(companyFileds,10)
  return [200, company];
});
mock.onGet('/company_detail').reply(function(config){
  var companyDetailFileds = require('./mock/company')
  var company = getFakeData(companyDetailFileds,1)[0]
  return [200,company]
})
mock.onGet('/company_fields').reply(function(config){
  var companyFields = require('./mock/companyFields')
  return [200,companyFields]
})
module.exports = mockAxios;



