import React from 'react'
import { Table } from 'antd';
import api from '../../api';
import PanelBox from '../../components/PanelBox';
import { Radio } from 'antd';
import { Input } from 'antd';
//import { getCompanies} from '../../actions/company'
const Search = Input.Search;
const RadioButton = Radio.Button;
const RadioGroup = Radio.Group;
const columns = [{
  title: '所有公司',
  dataIndex: 'comName',
  sorter: true,
  fixed:'left'
  //render: name => `${name.first} ${name.last}`,
 // width: '20%', 
}, {
  title: '合同编号',
  dataIndex: 'contractNo',
 // width: '20%',
}, {
  title: '项目名称',
  dataIndex: 'projectName',
}, {
  title: '所有项目',
  dataIndex: 'comField',
}, {
  title: '客户名称',
  dataIndex: 'customerName',
}, {
  title: '客户类型',
  dataIndex: 'customerType',
}, {
  title: '合同人数',
  dataIndex: 'peopleNum',
}, {
  title: '起始时间',
  dataIndex: 'startTime',
}, {
  title: '结束时间',
  dataIndex: 'endTime',
}, {
  title: '合同单价',
  dataIndex: 'unitPrice',
}, {
  title: '合同总额',
  dataIndex: 'totalPrice',
}, {
  title: '入账金额',
  dataIndex: 'income',
}];

export default class Contract extends React.Component {
  state = {
    data: [],
    pagination: {},
    loading: false,
  };
  handleTableChange = (pagination, filters, sorter) => {
    const pager = this.state.pagination;
    pager.current = pagination.current;
    this.setState({
      pagination: pager,
    });
    this.fetch({
      results: pagination.pageSize,
      page: pagination.current,
      sortField: sorter.field,
      sortOrder: sorter.order,
      ...filters,
    });
  }
  fetch = (params = {}) => {
    this.setState({ loading: true });
    api.get('/contracts', {
      params: {
        results: 10,
        ...params,
      },
      responseType: 'json'
    }).then((data) => {
      data = data.data;
     // console.log(data)
      const pagination = this.state.pagination;
      // Read total count from server
      // pagination.total = data.totalCount;
      pagination.total = 200;
      this.setState({
        loading: false,
        data: data,
        pagination,
      });
    });
  }
  componentDidMount () {
    this.fetch();
  }
  render() {
    return (
      <div>
        <div className = 'table-container-header'>
          <RadioGroup size="normal" onChange={this.handleRadioChange} defaultValue="2017">
            <RadioButton value="2015">2015</RadioButton>
            <RadioButton value="2016">2016</RadioButton>
            <RadioButton value="2017">2017</RadioButton>
          </RadioGroup>
         <Search
          placeholder="输入合同信息搜索"
          style={{ width: 250 ,marginRight:20}}
          onSearch={value => console.log(value)}
         />
        </div>
        <br/>
        <PanelBox title="合同信息">
          <Table columns={columns}
            rowKey={record => record.registered}
            dataSource={this.state.data}
            pagination={this.state.pagination}
            loading={this.state.loading}
            onChange={this.handleTableChange}
            scroll={{ x: 1800 }}
          />
        </PanelBox>
      </div>
    );
  }
}



